/**
 * Core grid component
 *
 * Heavily inspired by <https://github.com/suitcss/components-grid/>
 *
 * DO NOT apply dimension or offset utilities to the `grid` element. All cell
 * widths and offsets should be applied to child grid cells only.
 */
/**
 * Grid container
