  -webkit-appearance: none;
  /* 1 */
  background: #fff;
  border-color: #f60;
  border-style: solid;
  border-width: 1px;
  box-sizing: border-box;
  /* 2 */
  color: #f60;
  /* 3 */
  cursor: pointer;
  display: inline-block;
  font: inherit;
  /* 4 */
  line-height: normal;
  /* 5 */
  margin: 0;
  padding: 12px 24px;
  border-radius: 0;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  /* 6 */
  white-space: normal;
  /* 7 */
